.App {
  text-align: center;
  width: 98%; height: 85vh;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: 1fr 1fr 1fr;
  grid-template-areas: 
    "textbox textbox  dicbox"
    "textbox textbox  words"
    "textbox textbox words";
  
  gap: 1em;
  background-color: white;
  box-sizing: border-box;
  overflow-x: hidden;
  margin:auto;
}
@media only screen and (max-width: 768px){
  .App{grid-template-columns: 1fr;
      grid-template-rows: 1fr 1fr 1fr;
      grid-template-areas: 
      'textbox'
       'words'
        'dicbox';
     }
 
}
.title{
  text-align: center;
  box-sizing: border-box;
  display: flex;
  flex-direction: row;
  align-items: center;
  height:10vh;
  padding-left: 0.5em;
  justify-content: space-between;
  margin: 0px;
 margin-bottom: 0.1em;
}
.dicbox{
  grid-area: dicbox;
  text-align: left;
  box-sizing: border-box;
  overflow-x: hidden;
}

textarea{
  box-sizing: border-box;
  resize: none;
  padding-left: 2em;
  padding-top: 1em;
  padding-right: 2em;
  font-size: xx-large;
  grid-area: textbox;
  border: solid black;
 
  border-width: thin;
  margin: none;
}

ul{
  
  background-color: rgb(255, 255, 255);
  margin-top: 0px;
  padding-left: 0px;
  grid-area: words;
  border:solid black;
  overflow: scroll;
  list-style-type: none;
  border-width: thin;
  overflow-x: hidden;
  margin: none;
}
li{
  border-bottom: solid black;
  border-width: thin;
  padding-top: 1em;
  padding-bottom: 1em;
  overflow-x: hidden;
  font-size: x-large;
  margin: none;
}
.LoadingBlob {
  border: 16px solid #f3f3f3; /* Light grey */
  border-top: 16px solid #0c0e0f; /* Blue */
  border-radius: 50%;
  height:3em;
  width: 3em;

}

@keyframes spin {
  from { transform: rotate(0deg); }
  to { transform: rotate(359deg); }
}

@media (prefers-reduced-motion: no-preference) {
  .LoadingBlob {
    animation: spin 1s infinite linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

::-webkit-scrollbar {
  width: 12px;
  border-left: solid thin black;
}

::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0); 
  border-radius: 0px;
}

::-webkit-scrollbar-thumb {
color: black;
background-color: black;
  -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.5); 

}
button:hover{
  border:solid white !important;
  background-color: black !important;
  border-radius: 99px;
  color: white !important;
}
button{
  border-radius: 99px;
}
.modal{
  display:flex;
  align-items: center;
  justify-content: center;
  position: fixed;
  z-index: 1;
  left: 0;
  top:0;
  width:100%;
  height:100%;
  overflow:auto;
  background-color: rgb(0,0,0); /* Fallback color */
  background-color: rgba(0,0,0,0.4); /* Black w/ opacity */
}
/* Modal Content/Box */
.modal-content {
  display:flex;
  justify-content: space-between;
  background-color: #fefefe;
  padding: 20px;
  font-weight: bold;
  border:solid black thick;
  width: 80%; /* Could be more or less, depending on screen size */
}
/* The Close Button */
.close {
  background-color: red;
  color: white;
  font-size: xx-large;
  font-weight: bold;
  border-radius:0px;
  text-decoration: none;
  border:solid black;
}

.close:hover
 {
  color: white;
  text-decoration: none;
  cursor: pointer;
  border-radius:0px;
  background-color: rgb(247, 85, 85) !important;
  border:solid black !important;
}
#dicboxLoading{
  display:flex;
  justify-content: center;
  align-items: center;
  border:solid black thin;
  overflow: hidden;
}
.book{
  width:0.25em;
  height: 0.25em;
}